/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; margin-left: auto; margin-right: auto; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

.icon-facebook { width: 0.53em; height: 1em; fill: #000; }

body { background: #FFF; font-size: 50px; font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600))); font-family: 'Roboto Bold Condensed', sans-serif; }

@media (max-width: 600px) { body { font-size: 16px; } }

@media (min-width: 1000px) { body { font-size: 50px; } }

.container { width: 100%; max-width: 1000px; padding: 0 25px; margin: 0 auto; }

a { font-size: 17px; text-decoration: none; }

h2 { font-size: 24px; text-transform: uppercase; }

@font-face { font-family: 'Roboto Bold Condensed'; src: url("../fonts/RobotoCondensed-Bold.ttf") format("truetype"); }

@font-face { font-family: 'Roboto Condensed'; src: url("../fonts/RobotoCondensed-Regular.ttf") format("truetype"); }

@font-face { font-family: 'Arial MT'; src: url("../fonts/ARIALMT.ttf") format("truetype"); }

header { padding: 10px 0; }

@media screen and (max-width: 991px) { header { padding-bottom: 0; padding-top: 10px; } }

header .container { display: flex; justify-content: space-between; }

header .container .logo { display: flex; width: 150px; }

header .container .logo .text p { padding-top: 5px; padding-left: 5px; color: #189b33; text-transform: uppercase; font-size: 18px; font-weight: 400; letter-spacing: -1px; }

@media screen and (max-width: 991px) { header .container .logo .text p { padding-top: 12px; } }

header .container .contacts { position: relative; color: #252525; display: flex; flex-direction: column; }

header .container .contacts p { display: block; position: relative; font-size: 26px; font-weight: 400; font-family: 'Roboto Condensed', sans-serif; text-align: right; }

header .container .contacts p:before { position: absolute; content: ''; background-image: url("../img/phone.png"); background-size: 100%; width: 33px; height: 34px; top: 0; left: -38px; }

header .container .contacts p span { font-family: 'Roboto Bold Condensed', sans-serif; font-weight: 700; }

header .container .contacts a { position: absolute; text-align: center; width: 136px; background-color: #f6bd14; color: #2b2b2b; text-decoration: none; font-size: 14px; font-weight: 400; line-height: 31px; border-radius: 3px; top: 40px; right: 0; }

.navigation { background-color: #252525; border-top: 1px solid #f6bd14; border-bottom: 1px solid #f6bd14; }

.navigation .container .nav { padding: 20px 0; display: flex; }

@media screen and (max-width: 991px) { .navigation .container .nav { flex-wrap: wrap; justify-content: center; padding-bottom: 10px; padding-top: 10px; } }

.navigation .container .nav li { padding-right: 10px; padding-left: 7px; border-right: 1px solid #f6bd14; }

.navigation .container .nav li:first-child { padding-left: 0; }

.navigation .container .nav li a { display: block; color: #ffffff; font-size: 17px; font-weight: 400; line-height: 20px; text-transform: uppercase; }

.elements { box-shadow: inset 0 0 0 #ffffff; background-color: #898989; background-image: linear-gradient(to right, rgba(0, 0, 0, 0.22) 0%, rgba(255, 255, 255, 0.22) 50%, rgba(0, 0, 0, 0.22) 100%); background-image: url("../img/layer-28-copy.png"); background-position: 100%; }

.elements .container ul { display: flex; }

.elements .container ul .first { background-image: url("../img/layer-1.png"); }

.elements .container ul .second { background-image: url("../img/layer-2.png"); }

.elements .container ul .third { background-image: url("../img/layer-3.png"); }

.elements .container ul .fourth { background-image: url("../img/layer-4.png"); }

.elements .container ul li { position: relative; width: 25%; min-height: 337px; border: none; padding: 0; }

.elements .container ul li .top { position: absolute; top: -2px; width: 100%; padding: 20px; font-size: 24px; text-transform: uppercase; color: #ffffff; background-color: #164f36; opacity: 0.9; }

.elements .container ul li .bottom { position: absolute; bottom: 30px; width: 100%; padding: 5px 15px; font-size: 16px; font-weight: 400; font-family: 'Roboto Condensed', sans-serif; color: #ffffff; background-color: #164f36; opacity: 0.9; }

@media screen and (max-width: 991px) { .elements .container ul li .b-first { bottom: 10px; } }

.yellow { background-color: #f6bd14; padding-bottom: 30px; }

.yellow .container { display: flex; }

.yellow .container .siding { padding-bottom: 50px; border-right: 1px solid #164f36; }

.yellow .container .panels { padding-left: 20px; }

.yellow .container .siding, .yellow .container .panels { width: 50%; margin-top: 30px; }

.yellow .container .siding .top, .yellow .container .panels .top { padding-top: 15px; font-size: 24px; font-weight: 400; text-transform: uppercase; color: #252525; }

.yellow .container .siding p, .yellow .container .panels p { padding-top: 15px; font-family: 'Arial MT', sans-serif; font-size: 14px; font-weight: 400; padding-right: 50px; }

.yellow .container .siding ul, .yellow .container .panels ul { padding-top: 20px; display: flex; }

.yellow .container .siding ul li, .yellow .container .panels ul li { position: relative; margin-right: 5px; width: 72px; height: 74px; }

.yellow .container .siding ul li a, .yellow .container .panels ul li a { position: absolute; top: 40%; left: 3%; color: #252525; font-size: 14px; text-decoration: underline; text-transform: uppercase; display: none; }

@media screen and (max-width: 991px) { .yellow .container .siding ul li a, .yellow .container .panels ul li a { left: 6%; font-size: 12px; } }

.yellow .container .siding ul li:hover a, .yellow .container .panels ul li:hover a { display: block; }

.yellow .container .siding ul li img, .yellow .container .panels ul li img { width: 100%; height: 100%; object-fit: cover; box-sizing: border-box; }

.company { padding-top: 50px; padding-bottom: 50px; box-shadow: inset 0 0 0 #ffffff; background-color: #898989; background-image: linear-gradient(to right, rgba(0, 0, 0, 0.22) 0%, rgba(255, 255, 255, 0.22) 50%, rgba(0, 0, 0, 0.22) 100%); background-image: url("../img/layer-28-copy.png"); background-position: 100%; }

.company .container h2 { font-size: 24px; font-weight: 400; text-transform: uppercase; color: #252525; }

@media screen and (max-width: 991px) { .company .container h2 { text-align: center; } }

.company .container p { padding-top: 20px; color: #252525; font-family: 'Arial MT', sans-serif; font-size: 14px; }

.company .container ul { padding-top: 30px; display: flex; justify-content: space-between; }

@media screen and (max-width: 480px) { .company .container ul { flex-wrap: wrap; } }

.company .container ul li { display: flex; flex-direction: column; }

.company .container ul li span { text-align: center; max-width: 150px; font-size: 17px; text-transform: uppercase; }

.reviews { padding-top: 50px; padding-bottom: 40px; border-top: 2px solid #f6bd14; border-bottom: 2px solid #f6bd14; background-color: #164f36; background-image: linear-gradient(to right, rgba(0, 0, 0, 0.21) 0%, rgba(255, 255, 255, 0.21) 50%, rgba(0, 0, 0, 0.21) 100%); }

.reviews .container { display: flex; }

@media screen and (max-width: 991px) { .reviews .container { flex-direction: column; } }

.reviews .container h2 { font-size: 24px; color: #ffffff; text-transform: uppercase; }

@media screen and (max-width: 991px) { .reviews .container h2 { text-align: center; } }

.reviews .container .read-reviews { position: relative; width: 50%; }

@media screen and (max-width: 991px) { .reviews .container .read-reviews { width: 100%; } }

.reviews .container .read-reviews ul { padding-top: 30px; display: flex; }

.reviews .container .read-reviews ul h3 { color: #f6bd14; font-size: 14px; font-family: 'Arial MT', sans-serif; font-weight: 400; }

@media screen and (max-width: 991px) { .reviews .container .read-reviews ul h3 { font-size: 16px; } }

.reviews .container .read-reviews ul p { padding-top: 15px; padding-right: 35px; font-family: 'Arial MT', sans-serif; color: #ffffff; font-size: 14px; font-weight: 400; }

@media screen and (max-width: 991px) { .reviews .container .read-reviews ul p { font-size: 16px; } }

.reviews .container .read-reviews .show-all { position: absolute; right: 40px; color: #f6bd14; font-family: 'Arial MT', sans-serif; font-size: 14px; font-weight: 400; text-decoration: underline; display: inline-block; padding: 5px; border: 1px solid #f6bd14; border-radius: 3px; }

@media screen and (max-width: 991px) { .reviews .container .read-reviews .show-all { font-size: 16px; margin-top: 20px; display: block; width: 100%; right: 0; text-align: center; padding: 10px 0; } }

.reviews .container .specialist { width: 50%; border-left: 1px solid #61c177; padding-left: 20px; }

@media screen and (max-width: 991px) { .reviews .container .specialist { padding-top: 80px; border: none; width: 100%; padding-left: 0; } }

@media screen and (max-width: 991px) { .reviews .container .specialist form { text-align: center; } }

.reviews .container .specialist form .full { width: 92.5%; }

@media screen and (max-width: 991px) { .reviews .container .specialist form .full { width: 100%; } }

.reviews .container .specialist form input { display: inline-block; border: 1px solid #f6bd14; border-radius: 3px; width: 45%; padding: 8px 10px; color: #252525; font-family: 'Arial MT', sans-serif; font-size: 14px; }

.reviews .container .specialist form input::placeholder { color: #898989; font-family: 'Arial MT', sans-serif; font-size: 14px; }

@media screen and (max-width: 991px) { .reviews .container .specialist form input { width: 100%; } }

.reviews .container .specialist form textarea { margin-top: 18px; border: 1px solid #f6bd14; border-radius: 3px; width: 92.5%; padding: 8px 10px; color: #252525; font-family: 'Arial MT', sans-serif; font-size: 14px; }

.reviews .container .specialist form textarea::placeholder { color: #898989; font-family: 'Arial MT', sans-serif; font-size: 14px; }

@media screen and (max-width: 991px) { .reviews .container .specialist form textarea { width: 100%; } }

.reviews .container .specialist form button { text-align: center; color: #ffffff; font-family: 'Roboto Bold Condensed', sans-serif; font-size: 20px; line-height: 42px; text-transform: uppercase; display: inline-block; background-color: #f6bd14; width: 45%; height: 42px; border-radius: 3px; }

@media screen and (max-width: 991px) { .reviews .container .specialist form button { display: block; width: 100%; } }

.news { padding-top: 40px; padding-bottom: 40px; border-bottom: 1px solid #ffffff; background-color: #898989; background-image: linear-gradient(to right, rgba(0, 0, 0, 0.22) 0%, rgba(255, 255, 255, 0.22) 50%, rgba(0, 0, 0, 0.22) 100%); }

.news .container h2 { font-size: 24px; }

@media screen and (max-width: 991px) { .news .container h2 { text-align: center; } }

.news .container ul { display: flex; padding-top: 30px; }

@media screen and (max-width: 991px) { .news .container ul { flex-direction: column; } }

.news .container ul li { width: 50%; display: flex; }

@media screen and (max-width: 991px) { .news .container ul li { width: 100%; margin-top: 15px; } }

.news .container ul li .img img { width: 111px; height: 111px; }

@media screen and (max-width: 991px) { .news .container ul li .img img { width: 70px; height: 70px; } }

.news .container ul li .text { padding-left: 20px; }

.news .container ul li .text h3 { font-family: 'Roboto Bold Condensed', sans-serif; font-size: 14px; font-weight: 400; text-transform: uppercase; color: #164f36; }

.news .container ul li .text h3 span { display: block; font-family: 'Roboto Condensed', sans-serif; font-size: 14px; font-weight: 400; color: #636363; }

.news .container ul li .text p { font-family: 'Arial MT', sans-serif; font-size: 13px; font-weight: 400; padding-right: 10px; }

.gallery { box-shadow: inset 0 0 0 #ffffff; background-color: #898989; background-image: linear-gradient(to right, rgba(0, 0, 0, 0.22) 0%, rgba(255, 255, 255, 0.22) 50%, rgba(0, 0, 0, 0.22) 100%); }

footer { padding-top: 10px; padding-bottom: 20px; }

footer .container { display: flex; }

footer .container .footer-logo { display: flex; flex-direction: column; }

footer .container .footer-logo img { width: 150px; height: 63px; }

footer .container .footer-logo span { font-family: 'Arial MT', sans-serif; font-size: 13px; font-weight: 400; text-transform: uppercase; }

footer .container .footer-links { padding-left: 40px; }

footer .container .footer-links .nav { padding: 20px 0; display: flex; }

@media screen and (max-width: 991px) { footer .container .footer-links .nav { flex-wrap: wrap; justify-content: center; padding-bottom: 10px; padding-top: 10px; } }

footer .container .footer-links .nav li { padding-right: 10px; }

footer .container .footer-links .nav li:first-child { padding-left: 0; }

footer .container .footer-links .nav li a { display: block; color: #252525; font-size: 14px; font-weight: 400; line-height: 20px; text-transform: uppercase; }

footer .container .footer-links .contacts { display: flex; justify-content: space-between; }

footer .container .footer-links .contacts p { padding-left: 20px; display: block; font-size: 18px; font-weight: 400; font-family: 'Roboto Condensed', sans-serif; text-align: right; }

footer .container .footer-links .contacts p span { font-size: 20px; font-family: 'Roboto Bold Condensed', sans-serif; font-weight: 700; }

footer .container .footer-links .contacts span { font-family: 'Arial MT', sans-serif; font-weight: 400; font-size: 14px; text-decoration: underline; }
